import YgoCard from "../../YgoCard.vue";
import { DeckPart, } from "../../../../../core/src/main";
import { BDropdownItemButton, BModal } from "bootstrap-vue";
import { applicationContainer } from "../../../inversify.config";
import { APPLICATION_TYPES } from "../../../types";
import { computed, defineComponent, ref } from "@vue/composition-api";
import { appStore } from "../../../composition/appStore";
const deckService = applicationContainer.get(APPLICATION_TYPES.DeckService);
export default defineComponent({
    components: {
        YgoCard,
        BModal,
        BDropdownItemButton,
    },
    props: {},
    setup(props, context) {
        const goingFirst = ref(true);
        const drawnCards = ref([]);
        const deck = computed(() => appStore(context).state.deck.active);
        const hasMainDeckCards = computed(() => deck.value.parts[DeckPart.MAIN].length > 0);
        const draw = () => {
            drawnCards.value = deckService.getSimulatedStartingHand(deck.value, goingFirst.value);
        };
        const setGoingFirst = (val) => {
            goingFirst.value = val;
            draw();
        };
        return {
            goingFirst,
            hasMainDeckCards,
            drawnCards,
            setGoingFirst,
            draw,
        };
    },
});
