import { defineComponent } from "@vue/composition-api";
import { applicationContainer } from "../../../inversify.config";
import { APPLICATION_TYPES } from "../../../types";
import { BDropdownItemButton } from "bootstrap-vue";
import { copyText } from "../../../../../ui/src/main";
import { appStore } from "../../../composition/appStore";
import { showSuccess } from "../../../composition/feedback";
const deckExportService = applicationContainer.get(APPLICATION_TYPES.DeckExportService);
export default defineComponent({
    components: { BDropdownItemButton },
    props: {},
    setup: (props, context) => {
        const copyList = () => {
            const deck = appStore(context).state.deck.active;
            const deckList = deckExportService.toShareableText(deck);
            copyText(deckList, document);
            showSuccess(context, "Successfully copied deck list to clipboard.", "deck-tool__portal");
        };
        return { copyList };
    },
});
