import { CardTypeGroup } from "../../../../core/src/main";
import { computed, defineComponent } from "@vue/composition-api";
import YgoCard from "../YgoCard.vue";
import Draggable from "vuedraggable";
export default defineComponent({
    props: {
        matches: {
            required: true,
            type: Array,
        },
        canMove: {
            required: true,
            type: Function,
        },
    },
    components: {
        YgoCard,
        Draggable,
    },
    setup(props) {
        const CARD_DISPLAY_LIMIT = 50;
        const limitedMatches = computed(() => props.matches.slice(0, CARD_DISPLAY_LIMIT));
        const typeText = (card) => card.type.group === CardTypeGroup.MONSTER
            ? card.type.name
            : card.type.group;
        const subTypeText = (card) => card.type.group === CardTypeGroup.MONSTER
            ? `${card.attribute}/${card.subType}`
            : card.subType;
        return { limitedMatches, typeText, subTypeText };
    },
});
