import { applicationContainer } from "../../inversify.config";
import { APPLICATION_TYPES } from "../../types";
import { SortingOrder, SortingStrategy, } from "../../../../core/src/main";
import YgoFilter from "../YgoFilter.vue";
import YgoSortingOptions from "./YgoSortingOptions.vue";
import YgoBuilderMatches from "./YgoBuilderMatches.vue";
import { computed, defineComponent, reactive, ref, } from "@vue/composition-api";
import { appStore } from "../../composition/appStore";
import { dataLoaded } from "../../composition/dataLoaded";
import { BSidebar } from "bootstrap-vue";
const cardDatabase = applicationContainer.get(APPLICATION_TYPES.CardDatabase);
const sortingService = applicationContainer.get(APPLICATION_TYPES.SortingService);
const filterService = applicationContainer.get(APPLICATION_TYPES.FilterService);
const cardService = applicationContainer.get(APPLICATION_TYPES.CardService);
export default defineComponent({
    props: {
        canMove: {
            required: true,
            type: Function,
        },
    },
    components: {
        YgoFilter,
        YgoSortingOptions,
        YgoBuilderMatches,
        BSidebar,
    },
    setup(props, context) {
        const createDefaultFilter = () => {
            return {
                name: null,
                typeGroup: null,
                type: null,
                subType: null,
                attribute: null,
                level: null,
                linkMarker: [],
                archetype: null,
                format: null,
                banState: null,
                sets: [],
            };
        };
        const filter = reactive(createDefaultFilter());
        const resetFilter = () => {
            const defaultFilter = createDefaultFilter();
            filter.name = defaultFilter.name;
            filter.typeGroup = defaultFilter.typeGroup;
            filter.type = defaultFilter.type;
            filter.subType = defaultFilter.subType;
            filter.attribute = defaultFilter.attribute;
            filter.level = defaultFilter.level;
            filter.linkMarker = defaultFilter.linkMarker;
            filter.archetype = defaultFilter.archetype;
            filter.format = defaultFilter.format;
            filter.banState = defaultFilter.banState;
            filter.sets = defaultFilter.sets;
        };
        const sortingOptions = ref({
            strategy: SortingStrategy.DEFAULT,
            order: SortingOrder.DESC,
        });
        const format = computed(() => appStore(context).state.format.active);
        const formatCards = computed(() => {
            if (!loaded.value) {
                return [];
            }
            const uniqueCards = cardService
                .getUniqueByName(cardDatabase.getCards())
                .filter((card) => card.type.deckParts.size > 0); // Only show cards that can be added to at least one deck-part
            return filterService.filter(uniqueCards, {
                format: format.value,
            });
        });
        const filteredCards = computed(() => {
            const filtered = filterService.filter(formatCards.value, {
                ...filter,
                format: format.value,
            });
            return sortingService.sort(filtered, sortingOptions.value);
        });
        const loaded = dataLoaded(context);
        return {
            filter,
            sortingOptions,
            loaded,
            formatCards,
            filteredCards,
            resetFilter,
        };
    },
});
