const showFeedback = (context, message, cssClass, variant) => context.root.$bvToast.toast(message, {
    variant: variant,
    noCloseButton: true,
    solid: true,
    toastClass: cssClass,
});
export const showInfo = (context, message, cssClass) => showFeedback(context, message, cssClass, "info");
export const showSuccess = (context, message, cssClass) => showFeedback(context, message, cssClass, "success");
export const showWarning = (context, message, cssClass) => showFeedback(context, message, cssClass, "warning");
export const showError = (context, message, cssClass) => showFeedback(context, message, cssClass, "danger");
