import { RandomizationStrategy, } from "../../../../../core/src/main";
import { applicationContainer } from "../../../inversify.config";
import { APPLICATION_TYPES } from "../../../types";
import { BDropdownGroup, BDropdownItemButton, BModal } from "bootstrap-vue";
import { DECK_REPLACE } from "../../../store/modules/deck";
import { computed, defineComponent, ref } from "@vue/composition-api";
import YgoFilter from "../../YgoFilter.vue";
import VSelect from "vue-select";
import { appStore } from "../../../composition/appStore";
import { dataLoaded } from "../../../composition/dataLoaded";
const deckRandomizationService = applicationContainer.get(APPLICATION_TYPES.DeckRandomizationService);
export default defineComponent({
    components: {
        YgoFilter,
        VSelect,
        BModal,
        BDropdownItemButton,
        BDropdownGroup,
    },
    props: {},
    setup(props, context) {
        const strategies = Object.values(RandomizationStrategy);
        const strategy = ref(RandomizationStrategy.ARCHETYPE_2);
        const filter = ref({
            sets: [],
        });
        const format = computed(() => appStore(context).state.format.active);
        const randomize = () => {
            const randomizedDeck = deckRandomizationService.randomize(strategy.value, {
                ...filter.value,
                format: format.value,
            });
            appStore(context).commit(DECK_REPLACE, { deck: randomizedDeck });
        };
        const loaded = dataLoaded(context);
        return {
            strategy,
            strategies,
            filter,
            loaded,
            randomize,
        };
    },
});
