import { defineComponent, ref } from "@vue/composition-api";
import { getLogger, } from "../../../../../core/src/main";
import { applicationContainer } from "../../../inversify.config";
import { APPLICATION_TYPES } from "../../../types";
import { BDropdownItemButton, BModal } from "bootstrap-vue";
import { DECK_REPLACE } from "../../../store/modules/deck";
import { appStore } from "../../../composition/appStore";
import { showError, showSuccess } from "../../../composition/feedback";
const deckUriEncodingService = applicationContainer.get(APPLICATION_TYPES.DeckUriEncodingService);
const logger = getLogger("YgoImportYdkeUrl");
export default defineComponent({
    components: { BDropdownItemButton, BModal },
    props: {},
    setup: function (props, context) {
        const modal = ref();
        const ydkeUrl = ref("");
        const onInput = () => {
            let deck;
            try {
                deck = deckUriEncodingService.fromUri(ydkeUrl.value);
            }
            catch (e) {
                logger.error("Could not read YDKe URL!", e);
                showError(context, "Could not read YDKe URL.", "deck-tool__portal");
                return;
            }
            appStore(context).commit(DECK_REPLACE, {
                deck,
            });
            showSuccess(context, "Successfully imported YDKe URL.", "deck-tool__portal");
            modal.value.hide();
        };
        const onHide = () => {
            ydkeUrl.value = "";
        };
        return { ydkeUrl, modal, onInput, onHide };
    },
});
