import { defineComponent } from "@vue/composition-api";
import { getLogger, } from "../../../../../core/src/main";
import { applicationContainer } from "../../../inversify.config";
import { APPLICATION_TYPES } from "../../../types";
import { BDropdownItemButton } from "bootstrap-vue";
import { readFile, uploadFile } from "../../../../../ui/src/main";
import { DECK_REPLACE } from "../../../store/modules/deck";
import { appStore } from "../../../composition/appStore";
import { showError, showSuccess, showWarning, } from "../../../composition/feedback";
const deckFileService = applicationContainer.get(APPLICATION_TYPES.DeckFileService);
const logger = getLogger("YgoImportDeckFile");
export default defineComponent({
    components: { BDropdownItemButton },
    props: {},
    setup: (props, context) => {
        const importDeckFile = async (file) => {
            const fileContent = await readFile(file);
            const result = deckFileService.fromFile({
                fileContent,
                fileName: file.name,
            });
            appStore(context).commit(DECK_REPLACE, {
                deck: result.deck,
            });
            return result;
        };
        const processUpload = (file) => {
            importDeckFile(file)
                .then((result) => {
                if (result.missing.length > 0) {
                    showWarning(context, `${result.missing.length} cards could not be imported!`, "deck-tool__portal");
                }
                else {
                    showSuccess(context, "Successfully imported deck file.", "deck-tool__portal");
                }
            })
                .catch((e) => {
                logger.error("Could not read deck file!", e);
                showError(context, "Could not read deck file.", "deck-tool__portal");
            });
        };
        const openFileDialog = () => {
            uploadFile(".ydk", (files) => {
                if (files != null && files.length > 0) {
                    processUpload(files[0]);
                }
            }, document);
        };
        return { openFileDialog };
    },
});
